.black-nav{
  display: flex;
  background: black;
  width: 100%;
  color: white;
  padding-left: 20px;
}


.list{
  text-align: start;
  margin: 10px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.5);
}

.modal{
  background-color: rgb(216, 216, 216);
  color: black;
  text-align: start;
  margin: 10px;
  padding: 20px;
  width: 50%;
}

.list h4,span{
  cursor: pointer;
  user-select: none;
}

input{
  margin: 10px 20px;
  padding: 5px;
  font-size: 20px;
}
input~button{
  padding: 10px;
  margin: 0 0 0 40px;
}